@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700,800,900);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F4F4F4;

  /* background: rgb(26, 49, 95);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(26, 49, 95, 1)), color-stop(10%, rgba(26, 49, 95, 1)), color-stop(24%, rgba(29, 108, 141, 1)), color-stop(37%, rgba(41, 136, 151, 1)), color-stop(77%, rgba(39, 45, 100, 1)), color-stop(90%, rgba(26, 49, 95, 1)), color-stop(100%, rgba(26, 49, 95, 1))); */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.errorMessage {
  color: red;
  font-size: 0.75em;
  display: relative;
}

.frame {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 400px;
  height: 400px;
  margin-top: -200px;
  margin-left: -200px;
  overflow: hidden;
  background-color: transparent;
  color: #415868;
}

.modal {
  width: 280px;
  height: 210px;
  top: 95px;
  left: 60px;
  background: #fff;
  border-radius: 3px;
  box-shadow: 4px 8px 12px 0 rgba(0, 0, 0, 0.4);
  text-align: center;
  overflow: hidden;
  animation: show-modal 0.7s ease-in-out;
}

.hide {
  animation: hide-modal 0.6s ease-in-out both;
}

img {
  margin-top: 24px;
}

.title {
  display: block;
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  margin: 14px 0 5px 0;
}

.loginErrorMessage {
  font-size: 14px;
  font-weight: 300;
  line-height: 19px;
  margin: 0;
  padding: 0 30px;
}

.button {
  position: absolute;
  height: 40px;
  bottom: 0;
  left: 0;
  right: 0;
  background: #f65656;
  color: #fff;
  line-height: 40px;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}

@keyframes show-modal {
  0% {
    transform: scale(0);
  }
  60% {
    transform: scale(1.1);
  }
  80% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes hide-modal {
  0% {
    transform: scale(1);
  }
  20% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(0);
  }
}


.container {
    margin: 0 6rem 3rem 3rem;
}
.userCard {
    top: 5.55rem;
    position: absolute;
    width: 300px;
    transform: translateX(-43%);
    overflow: hidden;
    border: 1px solid white;
    border-top: none;
}
.mainView {
    height: calc(100vh - 89px);
    margin-top: 89px;
    padding-top: 2rem;
}
.unknowPage {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 3rem;
  height: 100vh;
}

.errorPage-Text {
  text-align: center;
}

.image-error {
  width: 45%;
}

.backHomeButton {
  margin-top: 10%;
  background-color: rgb(3, 103, 162);
  border-radius: 25px;
  border: none;
  font-size: 50px;
  color: whitesmoke;
  padding: 20px;
  outline: none;
}

.backHomeButton:hover {
  border: 1px solid yellow;
  cursor: pointer;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

